import { Component, OnInit } from '@angular/core';
import { CookieService } from 'ngx-cookie-service';


@Component({
  selector: 'app-rodo-overlay',
  templateUrl: './rodo-overlay.component.html',
  styleUrls: ['./rodo-overlay.component.css']
})
export class RodoOverlayComponent implements OnInit {

  title = 'app';

  userDataContentInacitve = "Niekatywna"
  userDataContentActive = "Aktywna"
  checked = true;
  shouldShow = false
  cookies = "Cookies itp."
  selected = null
  usersDataGather = "Przetwarzanie danych osobowych"
  gdprContest = "gdprContest"
  cookieValue: string
  date = new Date();
  mode = "main"


  changeMode(data:string){
    this.mode = data
  }
  

  partnersList = [
    {
      name: "Google",
      cookies: ["_ga", "_gat", "_gid"],
      cookieDestination: "zbieranie statystyk Google Analytics"
    },
    {
      name: "Google",
      cookies: ["IDE", "DSID"],
      cookieDestination: "reklamy w systemie .doubleclick.net"
    }
  ]



  isAgree(checked: boolean) {
    if (this.checked == true && this.selected === this.usersDataGather) {
      this.setCokies(1)
    } else if (this.selected === this.cookies || this.selected == null) {
      this.setCokies(1)
    } else {
      this.setCokies(0)
    }
  }

  setStyleOnSave() {
    if (this.cookieValue = this.cookieService.get(this.gdprContest)) {
      return { 'display': 'none' }
    } else {
      return { 'display': 'flex' }
    }
  }

  setStyle() {
    if (this.selected == null || this.selected == this.cookies) {
      return { 'display': 'block' }
    } else {
      return { 'display': 'none' }
    }
  }

  constructor(private cookieService: CookieService) { }

  setCokies(a) {

    if (a == 1) {

      this.date.setTime(this.date.getTime() + 365 * 1000 * 60 * 60 * 24)
      this.date.toUTCString()
    }

    else {
      this.date.setTime(this.date.getTime() + 1 * 1000 * 60 * 60 * 24)
      this.date.toUTCString()
    }
  
    this.cookieService.set(this.gdprContest, a, this.date)
  }



ngOnInit(): void {
  this.cookieValue = this.cookieService.get(this.gdprContest);
}

}



