import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';



import { AppComponent } from './app.component';
import { CookieService } from 'ngx-cookie-service';
import { RodoOverlayComponent } from './rodo-overlay/rodo-overlay.component';
import { MainContenComponent } from './rodo-overlay/main-conten.component';
import { CookiesContentComponent } from './rodo-overlay/cookies-content.component';
import { UserDataContentComponent } from './rodo-overlay/user-data-content.component';


@NgModule({
  declarations: [
    AppComponent,
    RodoOverlayComponent,
    MainContenComponent,
    CookiesContentComponent,
    UserDataContentComponent
  ],
  imports: [
    BrowserModule,
    FormsModule
  ],
  providers: [CookieService ],
  bootstrap: [AppComponent]
})
export class AppModule { }



